import {
  GET_PROJECTS,
  DELETE_PROJECT,
  ADD_PROJECT,
  GET_PROJECT
} from "../actions/types";

const initialState = {
  projects: [],
  project: {}
};

export default function(state = initialState, action) {
  switch (action.type) {
    // Here you can add all the actions: POST, GET, DEL, PATCH..
    case GET_PROJECTS:
      return {
        ...state,
        projects: action.payload
      };

    case GET_PROJECT:
      return {
        ...state,
        project: action.payload
      };

    case DELETE_PROJECT:
      return {
        ...state,
        projects: state.projects.filter(
          project => project.projectId !== action.payload
        )
      };
    case ADD_PROJECT:
      return {
        ...state
      };
    default:
      return state;
  }
}



