import React, { Component } from "react";
import Backlog from "./Backlog";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import { getBacklog } from "../../actions/backlogActions";

class ProjectBoard extends Component {
  constructor() {
    super();
    this.state = {
      errors: {}
    };
  }
  componentDidMount() {
    const { id } = this.props.match.params;
    this.props.getBacklog(id);
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }
  }

  render() {
    const { errors } = this.state;
    const { id } = this.props.match.params;

    let BoardContent;

    const { project_tasks } = this.props.backlog;

    //Algorithm code starts
    /*
      Challenge: now that we have this algorithm I suspect we can do the same
      thing for the UpdateProject component. You should be a 100% able to do this
      on your own. 
    */
    const boardAlgorithm = (errors, project_tasks) => {
      if (project_tasks === null) {
        //PROJECT_TASKS ARE NOT NULL ANYMORE
        if (errors.projectNotFound) {
          return (
            <div className="alert alert-danger text-center" role="alert">
              {errors.projectNotFound}
            </div>
          );
        } else {
          return <h1>Loading...</h1>;
        }
      } else {
        if (project_tasks.length < 1) {
          return (
            <div className="alert alert-info text-center" role="alert">
              No Project Tasks on this board
            </div>
          );
        } else {
          return <Backlog project_tasks={project_tasks} />;
        }
      }
    };
    //End of algorithm

    BoardContent = boardAlgorithm(errors, project_tasks);

    return (
      <div className="container">
        <Link to={`/addProjectTask/${id}`} className="btn btn-primary mb-3">
          <i className="fas fa-plus-circle"> Create Project Task</i>
        </Link>
        <br />
        <hr />
        {BoardContent}
      </div>
    );
  }
}

ProjectBoard.propTypes = {
  backlog: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired,
  getBacklog: PropTypes.func.isRequired
};

const mapStateToProps = state => ({
  backlog: state.backlog,
  errors: state.errors
});

export default connect(
  mapStateToProps,
  { getBacklog }
)(ProjectBoard);



