import axios from "axios";
import {
  GET_BACKLOG,
  GET_ERRORS,
  ADD_PROJECT_TASK,
  GET_PROJECT_TASK,
  DELETE_PROJECT_TASK
} from "./types";

export const getBacklog = backlog_id => async dispatch => {
  try {
    const res = await axios.get(`/api/backlog/${backlog_id}`);
    dispatch({
      type: GET_BACKLOG,
      payload: res.data
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

export const getProjectTask = (
  backlog_id,
  project_task_id,
  history
) => async dispatch => {
  try {
    const res = await axios.get(
      `/api/backlog/${backlog_id}/${project_task_id}`
    );
    dispatch({
      type: GET_PROJECT_TASK,
      payload: res.data
    });
  } catch (err) {
    /*
      Errors we can handle either way, I'm going to stick with back to dashboard if the user has no business with the backlog or pt we are not sending back to the project board because the users are either passing a backlog_id for a project not in their account or a wrong pt id. So I'm sending you back to your dashboard so you remember the projects that you are the owner of :) 
      Remember, this is only for those trying to force the URL
    */
    history.push("/dashboard");
  }
};

export const addProjectTask = (
  backlog_id,
  project_task,
  history
) => async dispatch => {
  try {
    const res = await axios.post(`/api/backlog/${backlog_id}`, project_task);
    history.push(`/projectBoard/${backlog_id}`);
    dispatch({
      type: ADD_PROJECT_TASK,
      payload: res.data
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

export const updateProjectTask = (
  backlog_id,
  pt_id,
  project_task,
  history
) => async dispatch => {
  try {
    const res = await axios.patch(
      `/api/backlog/${backlog_id}/${pt_id}`,
      project_task
    );
    history.push(`/projectBoard/${backlog_id}`);
    dispatch({
      type: ADD_PROJECT_TASK,
      payload: res.data
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

export const deleteProjectTask = (backlog_id, pt_id) => async dispatch => {
  if (
    window.confirm(
      `You are deleting project task ${pt_id}, this action cannot be undone`
    )
  ) {
    await axios.delete(`/api/backlog/${backlog_id}/${pt_id}`);
    dispatch({
      type: DELETE_PROJECT_TASK,
      payload: pt_id
    });
  }
};



