import React, { Component } from "react";
import "./App.css";
import Dashboard from "./components/Dashboard";
import Header from "./components/layout/Header";
import "bootstrap/dist/css/bootstrap.min.css";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { Provider } from "react-redux";
import store from "./store";
import Landing from "./components/Landing";
import AddProject from "./components/Project/AddProject";
import UpdateProject from "./components/Project/UpdateProject";
import Register from "./components/User/Register";
import Login from "./components/User/Login";
import jwt_decode from "jwt-decode";
import setAuthToken from "./utility/setAuthToken";
import { setCurrentUser, logout } from "./actions/authActions";
import SecuredRoute from "./utility/SecuredRoute";
import ProjectBoard from "./components/Project/ProjectBoard";
import AddProjectTask from "./components/Project/ProjectTasks/AddProjectTask";
import UpdateProjectTask from "./components/Project/ProjectTasks/UpdateProjectTask";
import "core-js/fn/array/find";
import "core-js/fn/array/includes";
import "core-js/fn/number/is-nan";

const jwtToken = localStorage.jwtToken;
if (jwtToken) {
  setAuthToken(jwtToken);
  const decoded_jwtToken = jwt_decode(jwtToken);
  store.dispatch(setCurrentUser(decoded_jwtToken));
  const currentTime = Date.now() / 1000;
  if (decoded_jwtToken.exp < currentTime) {
    store.dispatch(logout());
    window.location.href = "/";
  }
}

class App extends Component {
  render() {
    return (
      <Provider store={store}>
        <Router>
          <div className="App">
            <Header />
            {
              // "Public"
            }
            <Route exact path="/register" component={Register} />
            <Route exact path="/login" component={Login} />
            <Route exact path="/" component={Landing} />
            {
              // "Private"
            }
            <Switch>
              <SecuredRoute exact path="/addProject" component={AddProject} />
              <SecuredRoute exact path="/dashboard" component={Dashboard} />
              <SecuredRoute
                exact
                path="/updateProject/:id"
                component={UpdateProject}
              />
              <SecuredRoute
                exact
                path="/projectBoard/:id"
                component={ProjectBoard}
              />
              <SecuredRoute
                exact
                path="/addProjectTask/:id"
                component={AddProjectTask}
              />
              <SecuredRoute
                exact
                path="/updateProjectTask/:backlogid/:ptid"
                component={UpdateProjectTask}
              />
            </Switch>
          </div>
        </Router>
      </Provider>
    );
  }
}

export default App;



